import React from 'react';
import { connect } from 'react-redux';
import { Link, RouteComponentProps } from 'react-router-dom';
import { Button, Col, Row, Table } from 'reactstrap';
// tslint:disable-next-line:no-unused-variable
import { Translate, ICrudGetAllAction } from 'react-jhipster';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

import { IRootState } from 'app/shared/reducers';
import { getEntities } from './vehiculo.reducer';
import { IVehiculo } from 'app/shared/model/vehiculo.model';
// tslint:disable-next-line:no-unused-variable
import { APP_DATE_FORMAT, APP_LOCAL_DATE_FORMAT } from 'app/config/constants';

export interface IVehiculoProps extends StateProps, DispatchProps, RouteComponentProps<{ url: string }> {}

export class Vehiculo extends React.Component<IVehiculoProps> {
  componentDidMount() {
    this.props.getEntities();
  }

  render() {
    const { vehiculoList, match } = this.props;
    return (
      <div>
        <h2 id="vehiculo-heading">
          <Translate contentKey="concesionario4App.vehiculo.home.title">Vehiculos</Translate>
          <Link to={`${match.url}/new`} className="btn btn-primary float-right jh-create-entity" id="jh-create-entity">
            <FontAwesomeIcon icon="plus" />
            &nbsp;
            <Translate contentKey="concesionario4App.vehiculo.home.createLabel">Create new Vehiculo</Translate>
          </Link>
        </h2>
        <div className="table-responsive">
          <Table responsive>
            <thead>
              <tr>
                <th>
                  <Translate contentKey="global.field.id">ID</Translate>
                </th>
                <th>
                  <Translate contentKey="concesionario4App.vehiculo.marca">marca</Translate>
                </th>
                <th>
                  <Translate contentKey="concesionario4App.vehiculo.modelo">modelo</Translate>
                </th>
                <th>
                  <Translate contentKey="concesionario4App.vehiculo.precio">precio</Translate>
                </th>
                <th>
                  <Translate contentKey="concesionario4App.vehiculo.foto">Foto</Translate>
                </th>
                <th>
                  <Translate contentKey="concesionario4App.vehiculo.factura">Factura</Translate>
                </th>

                <th />
              </tr>
            </thead>
            <tbody>
              {vehiculoList.map((vehiculo, i) => (
                <tr key={`entity-${i}`}>
                  <td>
                    <Button tag={Link} to={`${match.url}/${vehiculo.id}`} color="link" size="sm">
                      {vehiculo.id}
                    </Button>
                  </td>
                  <td>{vehiculo.marca}</td>
                  <td>{vehiculo.modelo}</td>
                  <td>{vehiculo.precio}</td>
                  <td>{vehiculo.foto}</td>
                  <td>{vehiculo.factura ? <Link to={`factura/${vehiculo.factura.id}`}>{vehiculo.factura.id}</Link> : ''}</td>
                  <td className="text-right">
                    <div className="btn-group flex-btn-group-container">
                      <Button tag={Link} to={`${match.url}/${vehiculo.id}`} color="info" size="sm">
                        <FontAwesomeIcon icon="eye" />{' '}
                        <span className="d-none d-md-inline">
                          <Translate contentKey="entity.action.view">View</Translate>
                        </span>
                      </Button>
                      <Button tag={Link} to={`${match.url}/${vehiculo.id}/edit`} color="primary" size="sm">
                        <FontAwesomeIcon icon="pencil-alt" />{' '}
                        <span className="d-none d-md-inline">
                          <Translate contentKey="entity.action.edit">Edit</Translate>
                        </span>
                      </Button>
                      <Button tag={Link} to={`${match.url}/${vehiculo.id}/delete`} color="danger" size="sm">
                        <FontAwesomeIcon icon="trash" />{' '}
                        <span className="d-none d-md-inline">
                          <Translate contentKey="entity.action.delete">Delete</Translate>
                        </span>
                      </Button>
                    </div>
                  </td>
                </tr>
              ))}
            </tbody>
          </Table>
        </div>
      </div>
    );
  }
}

const mapStateToProps = ({ vehiculo }: IRootState) => ({
  vehiculoList: vehiculo.entities
});

const mapDispatchToProps = {
  getEntities
};

type StateProps = ReturnType<typeof mapStateToProps>;
type DispatchProps = typeof mapDispatchToProps;

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Vehiculo);
