import React from 'react';
import { connect } from 'react-redux';
import { Link, RouteComponentProps } from 'react-router-dom';
import { Button, Row, Col } from 'reactstrap';
// tslint:disable-next-line:no-unused-variable
import { Translate, ICrudGetAction } from 'react-jhipster';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

import { IRootState } from 'app/shared/reducers';
import { getEntity } from './vehiculo.reducer';
import { IVehiculo } from 'app/shared/model/vehiculo.model';
// tslint:disable-next-line:no-unused-variable
import { APP_DATE_FORMAT, APP_LOCAL_DATE_FORMAT } from 'app/config/constants';

export interface IVehiculoDetailProps extends StateProps, DispatchProps, RouteComponentProps<{ id: string }> {}

export class VehiculoDetail extends React.Component<IVehiculoDetailProps> {
  componentDidMount() {
    this.props.getEntity(this.props.match.params.id);
  }

  render() {
    const { vehiculoEntity } = this.props;
    return (
      <Row>
        <Col md="8">
          <h2>
            <Translate contentKey="concesionario4App.vehiculo.detail.title">Vehiculo</Translate> [<b>{vehiculoEntity.id}</b>]
          </h2>
          <dl className="jh-entity-details">
            <dt>
              <span id="marca">
                <Translate contentKey="concesionario4App.vehiculo.marca">marca</Translate>
              </span>
            </dt>
            <dd>{vehiculoEntity.marca}</dd>
            <dt>
              <span id="modelo">
                <Translate contentKey="concesionario4App.vehiculo.modelo">modelo</Translate>
              </span>
            </dt>
            <dd>{vehiculoEntity.modelo}</dd>
            <dt>
              <span id="precio">
                <Translate contentKey="concesionario4App.vehiculo.precio">precio</Translate>
              </span>
            </dt>
            <dd>{vehiculoEntity.precio}</dd>
            <dt>
              <span id="foto">
                <Translate contentKey="concesionario4App.vehiculo.foto">foto</Translate>
              </span>
            </dt>
            <dd>{vehiculoEntity.foto}</dd>
            <dt>
              <Translate contentKey="concesionario4App.vehiculo.factura">Factura</Translate>
            </dt>
            <dd>{vehiculoEntity.factura ? vehiculoEntity.factura.id : ''}</dd>
          </dl>
          <Button tag={Link} to="/entity/vehiculo" replace color="info">
            <FontAwesomeIcon icon="arrow-left" />{' '}
            <span className="d-none d-md-inline">
              <Translate contentKey="entity.action.back">Back</Translate>
            </span>
          </Button>
          &nbsp;
          <Button tag={Link} to={`/entity/vehiculo/${vehiculoEntity.id}/edit`} replace color="primary">
            <FontAwesomeIcon icon="pencil-alt" />{' '}
            <span className="d-none d-md-inline">
              <Translate contentKey="entity.action.edit">Edit</Translate>
            </span>
          </Button>
        </Col>
      </Row>
    );
  }
}

const mapStateToProps = ({ vehiculo }: IRootState) => ({
  vehiculoEntity: vehiculo.entity
});

const mapDispatchToProps = { getEntity };

type StateProps = ReturnType<typeof mapStateToProps>;
type DispatchProps = typeof mapDispatchToProps;

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(VehiculoDetail);
